.item-index-container {
  width: calc(100vw - 230px);
  height: calc(100vh - 50px);
  padding: 20px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* z-index: 1000; */
}

