.item-index-item-container {
  height: 250px;
  width: 250px;
  margin: 10px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.item-index-item {
  height: auto;
  width: auto;
  max-height: 250px;
  max-width: 250px;
  margin: 10px 10px;
}
