.nav-bar {
  min-width: 15%;
  display: flex;
  flex-direction: column;
  border-right: 1px solid black;
  padding-left: 10px;
}

.nav-bar-logo {
  width: 100%;
  height: 20%;
  min-height: 50px;
}

.nav-bar-item-container {
  height: 70%;
}

.nav-bar-item-container a,
.nav-bar-item-container div {
  text-decoration: none;
}

.nav-bar-item-container a:visited,
.nav-bar-item-container div {
  color: black;
  text-decoration: none;
}

.nav-bar-item-container a:hover,
.nav-bar-item-container div:hover {
  color: gray;
  cursor: pointer;
}

.menu-option {
  display: flex;
}

.sub-menu {
  display: flex;
  justify-content: flex-start;
  padding-left: 10px;
}
